export const STORE_FOCUS = "FOCUS_DONE"
export const HIDE_FOCUS = "FOCUS_HIDE"

export const storeFocus = (focus) => {
    return {
        type: STORE_FOCUS,
        focus,
        hidden: false
    }
}

export const hideFocus = (timeKey) => {
    return { 
        type: HIDE_FOCUS,
        key:timeKey
    }
}


