import { STORE_FOCUS,
         HIDE_FOCUS,  } from "../ReduxActions/FocusActions"

import { combineReducers } from "redux"

const focusHandler = (state = {}, action) => {
    switch (action.type){
        case STORE_FOCUS:
            return Object.assign({}, state,{
                focus: [...state.focus, action.focus]
            })
        case HIDE_FOCUS:
            Object.assign({}, state, {
                ...state.focus.map(el => el.key === action.key ? Object.assign({}, el, { hidden:true }) : el)
            })
        default: 
            return Object.assign({}, state,{
                focus:[]
            })
    }
}
const rootReducer = combineReducers({
    focusHandler
})  

export default rootReducer


